.modalOverlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 9998;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal {
  display: flex;
  justify-content: flex-end;
  position: fixed;
  top: 50%;
  left: 50%;
  width: 750px;
  height: 480px;
  border-radius: 8px;
  z-index: 9999;
  transform: translate(-50%, -50%);
  box-shadow: 0 0px 200px 1px rgb(237, 213, 189);
}

.img {
  width: 100%;
  height: 100%;
  border-radius: 8px;
  object-fit: cover;
}

.close {
  margin: 20px;
  position: absolute;
  z-index: 1;
  cursor: pointer;
}

.bookmark {
  position: absolute;
  bottom: 20px;
  left: 20px;
  color: #524242;
  cursor: pointer;
}

.bookcolor {
  position: absolute;
  bottom: 20px;
  left: 20px;
  color: yellow;
  cursor: pointer;
}

.title {
  bottom: 20px;
  left: 50px;
  position: absolute;
  font-weight: 800;
  color: white;
}
